.search-and-filter{
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: space-between;
    padding: 0 60px;
    margin: 60px 0px;
    margin-bottom: 90px;
}

@media screen and (max-width:835px) {
    
    .search-and-filter{
        padding: 0 0;
        margin: 60px 0 50px 20px;
        flex-flow: column nowrap;
        align-items: center;
        /* justify-content: flex-start; */
        /* background : red; */
        height: 150px;

    }
    .searchbox, .buttonClass{
        margin-bottom: 20px;
    }
    
}